h3 {
    /* background-color: aqua; */
    font-size: 3.8rem;
    margin-bottom: 0;
    margin-top: 45px;
    text-align: center;
}

p {
    /* background-color: blueviolet; */
    font-size: 1.9rem;
    font-style: italic;
    text-align: center;
    color: transparent;
}
p:hover{
    cursor: pointer;
    color: #90fff8;
}


.preguntas {
    /* background-color: #00ffff26; */
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.quiz {
    display: flex;
    width: 80%;
    flex-direction: column;
}

/*Responsive*/
@media screen and (max-width: 767px) {
    h3 {
        font-size: 2.7rem;
        margin-bottom: 0;
        margin-top: 20px;
    }

    p {
        /* background-color: blueviolet; */
        font-size: 1.2rem;
        margin-bottom: 40px;
    }
}