.pregunta {
    align-items: center;
    border-radius: 10px;
    border: solid 1px;
    display: flex;
    font-size: 1.5rem;
    justify-content: center;
    margin: 30px;
    min-height: 85px;
    padding: 20px;
    width: 40%;
}

.pregunta:hover {
    background-color: #6feeff35;
    cursor: pointer;
}

/*Responsive*/
@media screen and (max-width: 767px) {
    .pregunta {
        width: 90%;
        font-size: 1.1rem;
        padding: 8px;
        min-height: 45px;
        margin: 18px;
    }
}